.cta-block.text .title a, .cta-block.text h2 {
    color:$blue;
    font-size: 24px;
    margin-bottom:15px;
    text-decoration:none;
}

.cta-block.text .body {
    font-family: Merriweather;
    font-size:18px;
    line-height:30px;
}

.mini-gallery .slides {
    margin-bottom:25px;
}

.mini-gallery .image {
    border-radius:50%;
    overflow:hidden;
    height:250px;
    width:250px;
}

@media (min-width: $(desktop)) {

    .mini-gallery .image {
        height:325px;
        width:325px;
    }
}

.mini-gallery .image figure {
    display:block;
    width:100%;
    height:100%;
}

.mini-gallery .image img {
    height:400px;
    width:400px;
    -o-object-fit: cover;
       object-fit: cover;
}

.card{
    position:relative;
    -webkit-box-flex:0;
    -webkit-flex:0 0 auto;
        -ms-flex:0 0 auto;
            flex:0 0 auto;
    width:calc(99.99% * 12/12 - (30px - 30px * 12/12));
}

.card:nth-child(1n) {
    margin-right:30px;
}

.card:last-child {
    margin-right:0;
}

.card:nth-child(12n) {
    float:right;
}

.card:nth-child(12n) {
    margin-right:0;
}

@media (min-width: $(desktop)) {

    .card {
        -webkit-box-flex:0;
        -webkit-flex:0 0 auto;
            -ms-flex:0 0 auto;
                flex:0 0 auto;
        width:calc(99.99% * 6/12 - (30px - 30px * 6/12));
    }

    .card:nth-child(1n) {
        margin-right:30px;
    }

    .card:last-child {
        margin-right:0;
    }

    .card:nth-child(12n) {
        float:right;
    }

    .card:nth-child(12n) {
        margin-right:0;
    }
}

.card a {
    text-decoration:none;
}

.card .image{
    height:0px;
    padding-bottom:60%;
    overflow:hidden;
}

.card .image img {
    width:100%;
}

.card .text{
    padding:30px 30px;
    background:$white;
    position:relative;
    top:-65px;
    width:80%;
    text-align:center;
    text-decoration:none;
    -webkit-box-flex:0;
    -webkit-flex:0 0 auto;
        -ms-flex:0 0 auto;
            flex:0 0 auto;
    width:calc(99.99% * 11/12 - (30px - 30px * 11/12));
    margin-left:calc(99.99% * (-1/24 * -1) - (30px - 30px * (-1/24 * -1)) + 30px) !important;
}

.card .text:nth-child(1n) {
    margin-right:30px;
}

.card .text:last-child {
    margin-right:0;
}

.card .text:nth-child(12n) {
    float:right;
}

.card .text:nth-child(12n) {
    margin-right:0;
}

.card .text h2 {
    font-weight:600;
}

.card .text p {
    color:$white;
}

.testimonial .qoute {
    display:inline-block;
}
